/* main */
html {
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 1.5; }

body {
  color: #000000; }

.container {
  width: 1400px;
  padding: 0 115px;
  margin: 0 auto;
  max-width: 100%; }
  @media only screen and (max-width: 1279px) {
    .container {
      padding: 0 50px; } }
  @media only screen and (max-width: 767px) {
    .container {
      padding: 0 20px; } }

.no-scroll {
  overflow: hidden; }

/* components */
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  background-color: #ffffff; }
  .header::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 1px;
    background-color: #dfdfdf; }
  .header-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 0; }
  .header-logo {
    position: relative;
    z-index: 2;
    width: 142px; }
    @media only screen and (max-width: 1279px) {
      .header-logo {
        width: 80px; } }
    .header-logo__item {
      object-fit: contain; }
  .header-nav {
    color: #4a4a4a; }
    @media only screen and (max-width: 767px) {
      .header-nav {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100vw;
        height: 100vh;
        background-color: #ffffff;
        opacity: 0;
        visibility: hidden; } }
    .header-nav.active {
      visibility: visible;
      opacity: 1; }
    .header-nav__list {
      display: flex;
      align-items: center;
      white-space: nowrap; }
      @media only screen and (max-width: 767px) {
        .header-nav__list {
          flex-direction: column; } }
    .header-nav__item:not(:last-child) {
      margin-right: 40px; }
      @media only screen and (max-width: 767px) {
        .header-nav__item:not(:last-child) {
          margin-right: 0;
          margin-bottom: 20px; } }

.burger {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  width: 25px;
  height: 20px;
  padding: 0;
  background-color: transparent;
  border: none; }
  @media only screen and (min-width: 768px) {
    .burger {
      display: none; } }
  .burger.active .burger__line:first-child {
    transform: rotate(45deg); }
  .burger.active .burger__line:last-child {
    transform: rotate(-45deg); }
  .burger.active .burger__line:nth-child(2) {
    opacity: 0; }
  .burger__line {
    width: 100%;
    height: 2px;
    background-color: #4a4a4a;
    transform-origin: left;
    transition: 0.3s linear; }
    .burger__line:not(:last-child) {
      margin-bottom: 7px; }

.link {
  text-decoration: none;
  color: inherit; }
  @media (min-width: 1025px) {
    .link {
      transition: 0.3s linear; }
      .link:hover {
        color: #fb5d01; } }

